					<Input type="number" readonly class="rounded-l-md flex-1" id="order_id" v-model="order.id" />
					<Button class="rounded-r-md" @click="generateOrderId()" :disabled="order.id !== undefined">Generar</Button>
				</div>
			</template>
		</div>
		<div class="flex gap-3 items-center">
